import BACKEND_SERVER from './api';

export const getFrontend = async () => {
  const response = await fetch(`${BACKEND_SERVER}/api/frontend`);
  return response;
};

export const getBackend = async () => {
  const response = await fetch(`${BACKEND_SERVER}/api/backend`);
  return response;
};

export const suspend = async (node) => {
  const response = await fetch(`${BACKEND_SERVER}/api/suspend?node_addr=${node.addr}`, {
    method: 'POST'
  });
  return response;
};

export const revive = async (node) => {
  const response = await fetch(`${BACKEND_SERVER}/api/revive?node_addr=${node.addr}`, {
    method: 'POST'
  });
  return response;
};

export const showkeyvalue = async (node) => {
  const response = await fetch(`${BACKEND_SERVER}/api/showkeyvalue?node_addr=${node.addr}`, {
    method: 'POST'
  });
  return response;
};

