import { useEffect, useContext } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { UserContext } from '../../utils/UserContext';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { changePassword } from '../../utils/auth';

function Profile() {
  const {userState, setUserState} = useContext(UserContext);
  const handleChange = event => {
    event.preventDefault();
    // userState.loggedIn = true;
    // setUserState();

    // 👇️ access input values using name prop
    // console.log('first 👉️', event.target.formBasicUsername.value);
    // console.log('second 👉️', event.target.formBasicPassword.value);
    const username = userState.username;
    const password = event.target.formBasicPassword.value;


    changePassword(username, password).then((response) => {
      if (response.status === 200){
        event.target.reset();
        alert('Change successful');
      } else if (response.status === 401){
        alert('Invalid username or password!');
      }  
    })
      .catch((err) => {
        alert('Error during change password');
      });

    // 👇️ clear all input values in the form
    // event.target.reset();
    // navigate('/home', { replace: true });
  };
  return (
    <Container className='w-50 my-5'>
      <Form onSubmit={handleChange}>
        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label>Change password to</Form.Label>
          <Form.Control required type="password" placeholder="Password" />
        </Form.Group>
        <Button variant="primary" type="submit">
          Change password
        </Button>
      </Form>
    </Container>
  );
}

export default Profile;