import BACKEND_SERVER from './api';

export const compose = async (recipients, sender, subject, content) => {
  const response = await fetch(`${BACKEND_SERVER}/api/mail/compose`, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      recipients: recipients,
      sender: sender,
      subject: subject,
      content: content
    }),
  });
  return response;
};


export const deleteMail = async (mailId) => {
  const response = await fetch(`${BACKEND_SERVER}/api/mail/delete?mailId=${mailId}`, {
    method: 'DELETE'
  });
  return response;
};

export const viewAll = async () => {
  const response = await fetch(`${BACKEND_SERVER}/api/mail`);
  return response;
};
