import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import { suspend, revive, showkeyvalue } from '../../utils/admin';

function SingleBackendServer(props) {
  const { server, kvSetter } = props;
  const handleShutdown = () => {
    suspend(server);
  };
  const handleRevive = () => {
    revive(server);
  };

  const handleShowKV = () => {
    showkeyvalue(server).then(res => res.text()).then(data => kvSetter(data));
  };
  return (
    <Container className='bg-info m-3 p-3'>
      <Row>
        <Col>
          <div>
            {server.addr}
          </div>
          <div>
            {server.status}
          </div>
          <div>
            {server.is_primary ? 'Primary' : 'Not Primary'}
          </div>
        </Col>
        <Col>
          {server.status === 'RUNNING' ? <Button variant="danger" onClick={handleShutdown}>
            Suspend
          </Button> : <Button variant="success" onClick={handleRevive}>
            Revive
          </Button>}
        </Col>
        <Col>
          {server.status === 'RUNNING' ? <Button variant="primary" onClick={handleShowKV}>
            Show Key and Values
          </Button> : <>
          </>}
        </Col>
      </Row>
      
    </Container>
  );
}

export default SingleBackendServer;