import { useState, useEffect, useContext } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useNavigate, useLocation } from 'react-router-dom';
import { ReactMultiEmail, isEmail } from 'react-multi-email';
import './Compose.css';
import moment from 'moment';
import { compose } from '../../utils/mail';
import { UserContext } from '../../utils/UserContext';

function Compose() {
  const { userState, setUserState } = useContext(UserContext);
  const navigate = useNavigate();
  const location = useLocation();
  const [subject, setSubject] = useState('');
  const [content, setContent] = useState('');
  const [emails, setEmails] = useState([]);
  const [focused, setFocused] = useState(false);
  const handleSubmit = e => {
    e.preventDefault();
    const username = userState.username;
    compose(emails, username + '@localhost', subject, content).then(res => {
      e.target.reset();
      alert('Send successful');
      navigate('/mail', { replace: true });
    }).cateh((err) => {
      console.log(err);
    });
  };

  useEffect(() => {
    if (location.state !== null) {
      // const t = new Date(location.state.mail.time * 1000);
      // const formatted = moment(t).format('MMM Do YYYY, h:mm:ss a');
      const recipients = location.state.mail.recipients.join(', ');
      
      if (location.state.type === 'forward') {
        setSubject('Fwd: ' + location.state.mail.subject);
        setContent( `\n\n\n\n---------- Forwarded message ---------\nFrom: ${location.state.mail.sender}\nDate: ${location.state.mail.time}\nSubject: ${location.state.mail.subject}\nTo: ${recipients}\n${location.state.mail.content}`);
        
      } else if (location.state.type === 'reply') {
        setSubject('Re: ' + location.state.mail.subject);
        setEmails([location.state.mail.sender]);
        setContent( `\n\nOn ${location.state.mail.time}, ${location.state.mail.sender} wrote: \n${location.state.mail.content}`);
      }
        
    }
  }, []);

  const checkEmail = (str, options) => {
    if (str.endsWith('localhost')) {
      return true;
    }
    return isEmail(str, options);
  };

  const handleGoBack = e => {
    e.preventDefault();
    // navigate(-1);
    navigate({
      pathname: '/mail',
    }, { replace: true });
  };
  return (
    <Container>
      <Button className='mt-5 mb-3' variant="primary" type="submit" onClick={handleGoBack}>
          Go Back
      </Button>
      <Form onSubmit={handleSubmit}>
        <Form.Group className="mb-3" controlId="formSubject">
          <Form.Label>Subject</Form.Label>
          <Form.Control required type="text" placeholder="Subject" value={subject} onChange={(e)=>setSubject(e.target.value)} />
        </Form.Group>
        {/* <Form.Group className="mb-3" controlId="formRecipient">
          <Form.Label>Recipients</Form.Label>
          <Form.Control required type="email" placeholder="Enter email, separated by semicolon" />
        </Form.Group> */}
        <Form.Label>Recipients</Form.Label>
        <ReactMultiEmail
          placeholder='Input your email'
          emails={emails}
          onChange={(e) => {
            setEmails(e);
          }}
          validateEmail={checkEmail}
          autoFocus={true}
          onFocus={() => setFocused(true)}
          onBlur={() => setFocused(false)}
          getLabel={(email, index, removeEmail) => {
            return (
              <div data-tag key={index}>
                <div data-tag-item>{email}</div>
                <span data-tag-handle onClick={() => removeEmail(index)}>
                ×
                </span>
              </div>
            );
          }}
        />
        <Form.Group className="mb-3" controlId="formContent">
          <Form.Label>Content</Form.Label>
          <Form.Control required as="textarea" aria-label="With textarea" placeholder="Content" rows={15} value={content} onChange={(e) => setContent(e.target.value)} />
        </Form.Group>
        <Button variant="primary" type="submit">
          Send
        </Button>
      </Form>
    </Container>
  );
}

export default Compose;