import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useNavigate } from 'react-router-dom';
import { signup } from '../../utils/auth';

function Signup() {
  const navigate = useNavigate();
  const handleSubmit = event => {
    event.preventDefault();

    // 👇️ access input values using name prop
    // console.log('first 👉️', event.target.formBasicUsername.value);
    // console.log('second 👉️', event.target.formBasicPassword.value);
    const username = event.target.formBasicUsername.value;
    const password = event.target.formBasicPassword.value;


    signup(username, password).then((response) => {
      if (response.status === 201){
        event.target.reset();
        navigate('/login', { replace: true });
      } else if (response.status === 409){
        alert('Username taken!');
      }  
    })
      .catch((err) => {
        alert('Error during sign up');
      });

    
  };
  return (
    <Container className='w-50 my-5'>
      <Form onSubmit={handleSubmit}>
        <Form.Group className="mb-3" controlId="formBasicUsername">
          <Form.Label>User name</Form.Label>
          <Form.Control required type="username" placeholder="Enter user name" />
          <Form.Text className="text-muted">
            {"We'll never share your username with anyone else."}
          </Form.Text>
          <Form.Control.Feedback type="invalid">
              Please choose a username.
          </Form.Control.Feedback>
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label>Password</Form.Label>
          <Form.Control required type="password" placeholder="Password" />
          <Form.Text id="passwordHelpBlock" muted>
            Your password must be 8-20 characters long, contain letters and numbers,
            and must not contain spaces, special characters, or emoji.
          </Form.Text>
          <Form.Control.Feedback type="invalid">
              Please choose a password.
          </Form.Control.Feedback>
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicCheckbox">
          <Form.Check
            required
            label="Agree to terms and conditions"
            feedback="You must agree before submitting."
            feedbackType="invalid"
          />
        </Form.Group>
        <Button variant="primary" type="submit">
          Sign up
        </Button>
      </Form>
    </Container>
  );
}

export default Signup;