import BACKEND_SERVER from './api';

export const upload = async (fileContent, fileId) => {
  const response = await fetch(`${BACKEND_SERVER}/api/drive/upload?fileId=${fileId}`, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      fileContent: fileContent,
      fileId: fileId
    }),
  });
  return response;
};

export const download = async (fileId) => {
  const response = await fetch(`${BACKEND_SERVER}/api/drive/download?fileId=${fileId}`);
  return response;
};

export const deleteFile = async (fileId) => {
  const response = await fetch(`${BACKEND_SERVER}/api/drive/delete?fileId=${fileId}`, {
    method: 'DELETE'
  });
  return response;
};

export const listDir = async () => {
  const response = await fetch(`${BACKEND_SERVER}/api/drive`);
  return response;
};

export const updateDir = async (dirTree) => {
  let realDirTree = dirTree;
  if (realDirTree === null) {
    realDirTree = {
      fileId: '0',
      filename: '/',
      filetype: 'directory',
      children: [
      ]
    };
  }
  const response = await fetch(`${BACKEND_SERVER}/api/drive`, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(realDirTree),
  });
  return response;
};
