import { useState, useEffect, useCallback } from 'react';
import { useSearchParams, useNavigate, createSearchParams } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Modal from 'react-bootstrap/Modal';
import SingleFile from './SingleFile';
import './index.css';
import { download, listDir, upload, deleteFile, updateDir } from '../../utils/drive';
// import tree from './tree';

function CloudDrive() { 
  const reader = new FileReader();
  const navigate = useNavigate();
  const [searchParams, setSearchparams] = useSearchParams();
  const [currentPath, setCurrentPath] = useState([]);
  const [currentFiles, setCurrentFiles] = useState([]);
  const [dirTree, setDirTree] = useState({});

  useEffect(() => {
    listDir().then(res => res.json()).then(actualData => {
      if (actualData === null) {
        setDirTree({
          fileId: '0',
          filename: '/',
          filetype: 'directory',
          children: [
          ]
        });
      } else {
        setDirTree(actualData);
      }
      
    });
    
  }, []);

  useEffect(() => {
    if (Object.keys(dirTree).length === 0) {
      return;
    }
    updateDir(dirTree);
  }, [dirTree]);

  const str2path = (s) => {
    const pathArr = s.split('/');
    // console.log(pathArr);
    let resultArr = ['/'];
    for (let i = 1; i < pathArr.length - 1; i++){
      resultArr.push(pathArr[i] + '/');
    }
    return resultArr;
  };
  const path2str = (pathArr) => {
    const resultStr = pathArr.join('');
    return resultStr;
  };

  useEffect(() => {
    if (searchParams.get('path') !== null & searchParams.get('path') !== '') {
      const newPath = str2path(searchParams.get('path'));
      // console.log('set path via search param ' + path2str(newPath));
      setCurrentPath(newPath);
    }
    else {
      setCurrentPath(['/']);
      // searchParams.set('path', '/');
      // setSearchparams(searchParams, { replace: true });
    }
  }, [searchParams]);

  useEffect(() => {
    if (currentPath.length !== 0) {
      searchParams.set('path', path2str(currentPath));
      setSearchparams(searchParams, { replace: true });
    }
  }, [currentPath]);



  function compare( a, b ) {
    if ( a.filetype < b.filetype ){
      return -1;
    }
    if ( a.filetype > b.filetype ){
      return 1;
    }
    return 0;
  }

  const renderDirList = (path) => {
    // listDir(path2str(currentPath)).then(res => res.json()).then(newCurrentFiles => {
    //   newCurrentFiles = newCurrentFiles.sort(compare);
    //   setCurrentFiles(newCurrentFiles);
    // });
    // console.log(path);
    if (path.length === 0) {
      return;
    }
    if (Object.keys(dirTree).length === 0) {
      return;
    }
    let currNode = dirTree;
    for (let i = 1; i < path.length; i++){
      currNode = currNode.children.find(x => x.filename === path[i]);
    }
    let newCurrentFiles = currNode.children;
    newCurrentFiles = newCurrentFiles.sort(compare);
    // console.log('Current Files: ');
    // console.log(newCurrentFiles);
    setCurrentFiles([...newCurrentFiles]);
  };

  const addEntry = (file, path) => {
    console.log(file);
    console.log(path);
    console.log(dirTree);
    if (path.length === 0) {
      return;
    }
    let newTree = { ...dirTree };
    let currNode = newTree;
    let indexArr = [];
    for (let i = 1; i < path.length; i++){
      console.log(currNode);
      const newIndex = currNode.children.findIndex(x => x.filename === path[i]);
      currNode = currNode.children[newIndex];
      indexArr.push(newIndex);
    }
    currNode.children.push(file);
    setDirTree(newTree);
  };

  const removeEntry = (path) => {
    if (path.length === 0) {
      return;
    }
    console.log(path);
    let newTree = { ...dirTree };
    let currNode = newTree;
    let indexArr = [];
    for (let i = 1; i < path.length - 1; i++){
      const newIndex = currNode.children.findIndex(x => x.filename === path[i]);
      currNode = currNode.children[newIndex];
      indexArr.push(newIndex);
    }
    const lastIndex = currNode.children.findIndex(x => x.filename === path[path.length - 1]);
    currNode.children.splice(lastIndex, 1);
    // console.log(currNode);
    currNode = null;
    setDirTree(newTree);
  };

  useEffect(() => {
    // console.log('update triggered' + path2str(currentPath));
    if (currentPath === []) {
      return;
    }
    renderDirList(currentPath);
  }, [currentPath, dirTree]);

  const handleGoBack = (e) => {
    e.preventDefault();
    if (currentPath.length === 1) {
      return;
    }
    let newPath = [...currentPath];
    newPath = newPath.slice(0, newPath.length - 1);
    setCurrentPath(newPath);
  };

  const handleCreateDirectory = (e) => {
    e.preventDefault();
    const dirName = e.target.formNewDirectory.value;
    // upload('', e.target.formNewDirectory.value, currentPath, 'directory').then((res) => {
    //   // alert('create successful');
    //   e.target.reset();
    //   setCurrentPath(currentPath + dirName + '/');
    // });
    addEntry({
      fileId: Date.now().toString(),
      filename: dirName + '/',
      filetype: 'directory',
      children: []
    }, currentPath);
    handleCloseCreate();
    e.target.reset();
  };

  const handleClickFile = (file) => (e) => {
    e.preventDefault();
    if (file.filetype === 'directory') {
      let newPath = [...currentPath];
      newPath.push(file.filename);
      // console.log(newPath);
      setCurrentPath(newPath);
    }
  };

  const handleDownloadFile = (file) => (e) => {
    e.preventDefault();
    download(file.fileId).then((res) => 
      res.json()
    ).then((actualData) => {
      console.log(actualData);
      const linkSource = actualData.fileContent;
      const downloadLink = document.createElement('a');
      downloadLink.href = linkSource;
      downloadLink.download = file.filename;
      downloadLink.click();
    });
  };

  const handleDelete = (file)  => {
    // console.log('delete clicked');
    // deleteFile(file.fileId).then((res) => {
    //   renderDirList();
    // });
    // removeEntry([...currentPath, file.filename]);
    if (file.filetype === 'file') {
      deleteFile(file.fileId).then(res => {
        console.log(res.status);
      }).catch(err => {
        alert('failed!');
        return;
      });
    }
    removeEntry([...currentPath, file.filename]);
  };



  const handleMove = (file, path, newPath) => {
    // e.preventDefault();
    // const newPath = str2path(e.target.formMoveFilepath.value);
    const filepath = [...str2path(path), file.filename];
    console.log(filepath);
    addEntry(file, str2path(newPath));
    removeEntry(filepath);
  };



  const handleRename = (file, path, filename) => {
    // e.preventDefault();
    // console.log(e.target.formRenameFilename.value);
    // console.log(path);
    // let filename = e.target.formRenameFilename.value;
    // if (file.filetype === 'directory') {
    //   filename = filename + '/';
    // }
    const newFile = { ...file, filename: filename };
    const filepath = [...str2path(path), file.filename];
    addEntry(newFile, str2path(path));
    removeEntry(filepath);
  };
  
  const [showUpload, setShowUpload] = useState(false);
  const handleCloseUpload = () => setShowUpload(false);
  const handleShowUpload = () => setShowUpload(true);
  const [selectedFile, setSelectedFile] = useState();
  const handleFileSelect = e => {
    setSelectedFile(e.target.files[0]);
  };
  const handleUpload = e => {
    e.preventDefault();
    const fileId = Date.now().toString();
    // console.log(selectedFile);
    reader.readAsDataURL(selectedFile);
    reader.addEventListener('load', () => {
      // convert image file to base64 string
      // console.log(selectedFile.name);
      // console.log(reader.result);
      upload(reader.result, fileId).then((res) => {
        // todo selectedFile.name, currentPath, 'file'
        addEntry({
          fileId: fileId,
          filename: selectedFile.name,
          filetype: 'file',
        }, currentPath);
        handleCloseUpload();
      });
    }, false);
  };

  const [showCreate, setShowCreate] = useState(false);
  const handleCloseCreate = () => setShowCreate(false);
  const handleShowCreate = () => setShowCreate(true);

  return (
    <Container>
      <Modal show={showUpload} onHide={handleCloseUpload}>
        <Modal.Header closeButton>
          <Modal.Title>Upload to {path2str(currentPath)}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={handleUpload}>
            <Form.Group controlId="formFileLg" className="mb-3">
              <Form.Label size="lg">Choose file to upload to {currentPath}</Form.Label>
              <Form.Control type="file" size="lg" onChange={handleFileSelect} />
            </Form.Group>
            <Button variant="primary" type="submit">
              Upload selected file
            </Button>
          </Form>
        </Modal.Body>
        {/* <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseUpload}>
            Close
          </Button>
        </Modal.Footer> */}
      </Modal>
      <Modal show={showCreate} onHide={handleCloseCreate}>
        <Modal.Header closeButton>
          <Modal.Title>Create a directory in {path2str(currentPath)}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={handleCreateDirectory}>
            <Form.Group className="mb-3" controlId="formNewDirectory">
              <Form.Control placeholder="New directory" />
            </Form.Group>
            <Button variant="primary" type="submit">
              Create directory
            </Button>
          </Form>
        </Modal.Body>
        {/* <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseCreate}>
            Close
          </Button>
        </Modal.Footer> */}
      </Modal>
      <Row className='p-3'>
        <Col xs={2}>
          <Button variant="primary" type="submit" onClick={handleGoBack}>
            Go back
          </Button>
        </Col>
        <Col xs={6}>
          <h2>{path2str(currentPath)}</h2>
        </Col>
        <Col xs={4}>
          <Button  className='mx-1' variant="primary" type="submit" onClick={handleShowUpload}>
            Upload file
          </Button>
          <Button  className='mx-1' variant="primary" type="submit" onClick={handleShowCreate}>
            Create directory
          </Button>
        </Col>
      </Row>
      <Row className='p-3 heading'>
        <Col xs={6}>
          Name
        </Col>
        <Col xs={6}>
          Actions
        </Col>
      </Row>
      {currentFiles.length > 0 ? currentFiles.map((item, index) => {
        return (<SingleFile key={`${item.fileId}`} path={path2str(currentPath)} file={item} onClick={handleClickFile} onDownload={handleDownloadFile} onDelete={handleDelete} onRename={handleRename} onMove={handleMove} />);
      }) : <Row className='m-5'>Directory is currently empty</Row>}
    </Container>
  );
}

export default CloudDrive;