import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import moment from 'moment';
import { useNavigate } from 'react-router-dom';
import { deleteMail } from '../../utils/mail';

function ShowMail(props) {
  const { mail, refresh } = props;
  const navigate = useNavigate();
  // const t = new Date(mail.time * 1000);
  const onDelete = (e) => {
    e.preventDefault();
    deleteMail(mail.mailId);
    // refresh();
  };
  const onForward = (e) => {
    e.preventDefault();
    navigate('/mail/compose',{state:{mail: mail, type: 'forward'}});
  };
  const onReply = (e) => {
    e.preventDefault();
    navigate('/mail/compose',{state:{mail: mail, type: 'reply'}});
  };
  // t.setSeconds(1671221480 );
  // const formatted = moment(t).format('MMM Do YYYY, h:mm:ss a');
  return (
    <Container className='m-2'>
      <div className='d-flex justify-content-center my-2'>
        <Button className='mx-1' variant="primary" size='lg' onClick={onDelete}>Delete</Button>
        <Button className='mx-1' variant="primary" size='lg' onClick={onForward}>Forward</Button>
        <Button className='mx-1' variant="primary" size='lg' onClick={onReply}>Reply</Button>
      </div>
      <Row className='h5 my-2'>
        From: {mail.sender}
      </Row>
      <Row className='h5 my-2'>
        Date: {mail.time}
      </Row>
      <Row className='h5 my-2'>
        Subject: {mail.subject}
      </Row>
      <Row className='h5 my-2'>
        To: {mail.recipients.join(', ')}
      </Row>
      
      <Row className='my-2'>
        <span style={{ 'white-space': 'pre-wrap'}}>  
          {mail.content}
        </span> 
      </Row>
    </Container>
    
    
  );
}

export default ShowMail;