import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import { useNavigate } from 'react-router-dom';
import { UserContext } from '../../utils/UserContext';
import { useContext } from 'react';

function HomePage() {
  const { userState, setUserState } = useContext(UserContext);
  const navigate = useNavigate();
  const handleChangePage = (path) => {
    navigate(`${path}`);
  };
  return (
    <>{userState.loggedIn ? <Container className='w-75' fluid>
      
      <Row className="justify-content-md-center">
        <Col md="auto" className='m-5'><Button variant="primary" size='lg' onClick={(e) => handleChangePage('/drive')}>Drive</Button></Col>
        <Col md="auto" className='m-5'><Button variant="primary" size='lg' onClick={(e) => handleChangePage('/mail')}>Mail</Button></Col>
      </Row>
  
      <Row className="justify-content-md-center">
        <Col md="auto" className='m-5'><Button variant="primary" size='lg' onClick={(e) => handleChangePage('/admin')}>Admin</Button></Col>
        <Col md="auto" className='m-5'><Button variant="primary" size='lg' onClick={(e) => handleChangePage('/profile')}>Profile</Button></Col>
      </Row>
    </Container> : <Container className='w-75' fluid>
  

      <Row className="justify-content-md-center">
        <Col md="auto" className='m-5'><Button variant="primary" size='lg' onClick={(e) => handleChangePage('/login')}>Log In</Button></Col>
        <Col md="auto" className='m-5'><Button variant="primary" size='lg' onClick={(e) => handleChangePage('/signup')}>Sign up</Button></Col>
      </Row>
    </Container>}</>
    
    
  );
}

export default HomePage;