import { useState, useEffect } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import { getFrontend, getBackend } from '../../utils/admin';
import SingleFrontendServer from './SingleFrontendServer';
import SingleBackendServer from './SingleBackendServer';

function AdminConsole() {
  const [frontend, setFrontend] = useState([]);
  const [backend, setBackend] = useState([]);
  const [kv, setKV] = useState('');

  // useEffect(() => {
  //   getFrontend().then(res=> res.json()).then(actualData=> setFrontend(actualData));
  // }, []);

  useEffect(() => {
    getBackend().then(res => res.json()).then(actualData => {
      // console.log(actualData);
      setBackend(actualData);
    });
  }, []);

  useEffect(() => {
    getBackend().then(res => res.json()).then(actualData => {
      // console.log(actualData);
      setBackend(actualData);
    });
  }, []);

  const handleRefresh = () => {
    getBackend().then(res => res.json()).then(actualData => {
      // console.log(actualData);
      setBackend(actualData);
    });
  };
  // console.log(backend.map(e => {
  //   return 
  // }));
  return (
    <Container>
      <Button variant="primary" onClick={handleRefresh}>
            Refresh
      </Button>
      <h1>
        Backend
      </h1>
      <Row>
        {backend.map((serverArr, index) => {
          return (<Col key={index}>
            {
              serverArr.map(e => { 
                return (<SingleBackendServer key={e.addr} server={e} kvSetter={setKV} />);
              })
            }
          </Col>);
        })}
      </Row>
      {/* <h1>
        Frontend
      </h1>
      <Row>
        <Col>
          {frontend.map(e=> <SingleFrontendServer key={e.addr} server={e} />)}
        </Col>
        
      </Row> */}
      <Row>
        <span style={{ 'white-space': 'pre-wrap', 'word-wrap':'break-word'}}>  
          {kv}
        </span>  
      </Row>
    </Container>
  );
}

export default AdminConsole;