import { useContext } from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { UserContext } from '../utils/UserContext';
import { Link } from 'react-router-dom';

function NavBar() {
  const { userState, setUserState } = useContext(UserContext);

  return (
    <>
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand as={Link} to="/">PennCloud</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          {userState !== undefined & userState.loggedIn ?
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="me-auto">
                <Nav.Link  as={Link} to="/mail">Mail</Nav.Link>
                <Nav.Link  as={Link} to="/drive">Drive</Nav.Link>
                <NavDropdown title={userState.username} id="basic-nav-dropdown">
                  <NavDropdown.Item  as={Link} to="profile">Profile</NavDropdown.Item>
                  <NavDropdown.Item  as={Link} to="logout">Log Out</NavDropdown.Item>
                </NavDropdown>
                <Nav.Link  as={Link} to="/admin">Admin</Nav.Link>
              </Nav>
            </Navbar.Collapse> :
            <Navbar.Collapse id="basic-navbar-nav">
              <Nav className="me-auto">
                <NavDropdown title="User" id="basic-nav-dropdown">
                  <NavDropdown.Item  as={Link} to="login">Log In</NavDropdown.Item>
                  <NavDropdown.Item  as={Link} to="signup">Sign Up</NavDropdown.Item>
                </NavDropdown>
                <Nav.Link  as={Link} to="/admin">Admin</Nav.Link>
              </Nav>
            </Navbar.Collapse>
          }
          
        </Container>
      </Navbar>
    </>
  );
}

export default NavBar;