import {useState, useEffect, useMemo} from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import './App.css';
import HomePage from './pages/HomePage';
import SignupLogin from './pages/HomePage/SignupLogin';
import AdminConsole from './pages/AdminConsole';
import WebMail from './pages/WebMail';
import Compose from './pages/WebMail/Compose';
import CloudDrive from './pages/CloudDrive';
import Upload from './pages/CloudDrive/Upload';
import NavBar from './components/NavBar';
import Login from './pages/User/Login';
import Signup from './pages/User/Signup';
import Logout from './pages/User/Logout';
import Profile from './pages/User/Profile';
import { UserContext, defaultUserState } from './utils/UserContext';
import PrivateRoutes from './utils/PrivateRoutes';

function App() {
  const [state, setState] = useState(defaultUserState);
  useEffect(() => {
    const localAppState = localStorage.getItem('appState');
    if (localAppState !== null) {
      setState(JSON.parse(localAppState));
    }
  }, []);

  const appStateSetter = () => {
    localStorage.setItem('appState', JSON.stringify(state));
    setState({ ...state });
  };

  const appState = {
    userState: state,
    setUserState: appStateSetter,
  };
  const value = useMemo(() => (appState));
  return (
    <UserContext.Provider value={value}>
      <Router>
        <NavBar />
        <Routes>
          {/* <Route exact path="/" element={<Navigate to="/home" replace />} /> */}
          <Route exact path="/" element={<SignupLogin />} /> 
          <Route path="/" element={<PrivateRoutes />} >
            <Route path="drive" >
              <Route index element={<CloudDrive />} />
              <Route path='upload' element={<Upload />} />
            </Route>
            <Route path="mail" >
              <Route index element={<WebMail />} />
              <Route path='compose' element={<Compose />} />
            </Route>
            {/* <Route path="/home" element={<HomePage />} /> */}
            <Route path="profile" element={<Profile />} />
            <Route path="home" element={<HomePage />} />
          </Route>
          <Route path="admin" element={<AdminConsole />} />
          <Route path="/login" element={<Login />} />
          <Route path="/signup" element={<Signup />} />
          <Route path="/logout" element={<Logout />} />
          <Route
            path="*"
            element={<Navigate to="/" replace />}
          />
        </Routes>
        
      </Router>
    </UserContext.Provider>
  );
}

export default App;
