import { useState } from 'react';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import './SingleFile.css';

function SingleFile(props) {
  const { file, path,  onClick, onDownload, onDelete, onMove, onRename } = props;

  const [showRename, setShowRename] = useState(false);
  const handleCloseRename = () => setShowRename(false);
  const handleShowRename = () => setShowRename(true);
  const handleSubmitRename = (e) => {
    e.preventDefault();
    let filename = e.target.formRenameFilename.value;
    if (file.filetype === 'directory') {
      filename = filename + '/';
    }
    onRename(file, path, filename);
    handleCloseRename();
  };

  const [showMove, setShowMove] = useState(false);
  const handleCloseMove = () => setShowMove(false);
  const handleShowMove = () => setShowMove(true);

  // e.target.formMoveFilepath.value
  const handleSubmitMove = (e) => {
    e.preventDefault();
    const newPath = e.target.formMoveFilepath.value;
    onMove(file, path, newPath);
    handleCloseMove();
  };

  const [showDelete, setShowDelete] = useState(false);
  const handleCloseDelete = () => setShowDelete(false);
  const handleShowDelete = () => setShowDelete(true);

  const handleDelete = () => {
    onDelete(file);
    handleCloseDelete();
  };

  return (
    // <div className='file-entry-container' onClick={onClick}>
    // <Container fluid className='p-3 file-entry-container' onClick={onClick}>
    <Row className='p-3 file-entry-container'>
      <Modal show={showRename} onHide={handleCloseRename}>
        <Modal.Header closeButton>
          <Modal.Title>Rename {file.filetype === 'directory'? file.filename.substring(0, file.filename.length - 1) : file.filename}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={handleSubmitRename}>
            <Form.Group className="mb-3" controlId="formRenameFilename">
              {/* <Form.Label>User name</Form.Label> */}
              <Form.Control defaultValue={file.filetype === 'directory'? file.filename.substring(0, file.filename.length - 1) : file.filename} />
            </Form.Group>
            <Button variant="primary" type="submit">
                  Rename
            </Button>
          </Form>
        </Modal.Body>
      </Modal>
      <Modal show={showMove} onHide={handleCloseMove}>
        <Modal.Header closeButton>
          <Modal.Title>Move {file.filetype === 'directory'? file.filename.substring(0, file.filename.length - 1) : file.filename}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Move to: 
          <Form onSubmit={handleSubmitMove}>
            <Form.Group className="mb-3" controlId="formMoveFilepath">
              {/* <Form.Label>User name</Form.Label> */}
              <Form.Control defaultValue={path} />
            </Form.Group>
            <Button variant="primary" type="submit">
                  Move
            </Button>
          </Form>
        </Modal.Body>
      </Modal>
      <Modal show={showDelete} onHide={handleCloseDelete}>
        <Modal.Header closeButton>
          <Modal.Title>Delete {file.filetype} {file.filetype === 'directory'? file.filename.substring(0, file.filename.length - 1) : file.filename}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {file.filetype === 'directory' ? (file.children.length === 0 ?  <>Confirm delete {file.filename.substring(0, file.filename.length - 1)}? Action is irreversible! </> : <>You must remove all subdirectories and files before removing this folder</>): <>Confirm delete {file.filename}? Action is irreversible! </>}
        </Modal.Body>
        <Modal.Footer>
          {file.filetype === 'file' || file.children.length === 0?<Button variant="primary" onClick={handleDelete}>
            Confirm
          </Button>:<></>}
          {/* <Button variant="primary" onClick={handleDelete}>
            Confirm
          </Button> */}
          <Button variant="secondary" onClick={handleCloseDelete}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
      <Col xs={6} className='text-primary' onClick={onClick(file)}>
        {file.filename}
      </Col>
      <Col xs={6}>
        
        <Button className='mx-1' variant="secondary" type="submit" onClick={handleShowDelete}>
            Delete
        </Button>
        <Button className='mx-1' variant="primary" type="submit" onClick={handleShowRename}>
            Rename
        </Button>
        <Button className='mx-1' variant="primary" type="submit" onClick={handleShowMove}>
            Move
        </Button>
        {file.filetype === 'file' ? <Button className='mx-1' variant="primary" type="submit" onClick={onDownload(file)}>
            Download
        </Button> : ''}
      </Col>
          
    </Row>
    // </Container>
    // </div>
    
  );
}

export default SingleFile;