import { useEffect, useContext } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { UserContext } from '../../utils/UserContext';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { login } from '../../utils/auth';

function Login() {
  const location = useLocation();
  const navigate = useNavigate();
  const { userState, setUserState } = useContext(UserContext);

  useEffect(() => {
    if (userState.loggedIn) {
      const { from } = location.state || { from: { pathname: '/' } };
      navigate(from, { replace: true });
    }
  }, [userState, location, navigate]);

  const handleLogin = event => {
    event.preventDefault();
    // userState.loggedIn = true;
    // setUserState();

    // 👇️ access input values using name prop
    // console.log('first 👉️', event.target.formBasicUsername.value);
    // console.log('second 👉️', event.target.formBasicPassword.value);
    const username = event.target.formBasicUsername.value;
    const password = event.target.formBasicPassword.value;


    login(username, password).then((response) => {
      if (response.status === 200){
        event.target.reset();
        userState.loggedIn = true;
        userState.username = username;
        setUserState();
        // navigate('/home', { replace: true });
        const { from } = location.state || { from: { pathname: '/' } };
        navigate(from, { replace: true });
      } else if (response.status === 401){
        alert('Invalid username or password!');
      }  
    })
      .catch((err) => {
        alert('Error during log in');
      });

    // 👇️ clear all input values in the form
    // event.target.reset();
    // navigate('/home', { replace: true });
  };
  return (
    <Container className='w-50 my-5'>
      <Form onSubmit={handleLogin}>
        <Form.Group className="mb-3" controlId="formBasicUsername">
          <Form.Label>User name</Form.Label>
          <Form.Control required type="username" placeholder="Enter user name" />
        </Form.Group>
        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label>Password</Form.Label>
          <Form.Control required type="password" placeholder="Password" />
        </Form.Group>
        <Button variant="primary" type="submit">
          Login
        </Button>
      </Form>
    </Container>
  );
}

export default Login;