import { useState } from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import moment from 'moment';
import './MailEntry.css';

function MailEntry(props) {
  const { mail, onClick } = props;
  // const t = new Date(mail.time * 1000);
  // t.setSeconds(1671221480 );
  // const formatted = moment(t).format('MMM Do YYYY, h:mm:ss a');
  return (
    <Row className='p-3 mail-entry-container' onClick={onClick}>
      <Col xs={3} className='text-primary'>
        {mail.sender}
      </Col>
      <Col xs={3} className='text-secondary'>
        {mail.time}
      </Col>
      <Col xs={6}>
        {mail.subject.length > 30 ? mail.subject.slice(0, 30) : mail.subject}
      </Col>
    </Row>
    
  );
}

export default MailEntry;