import { useState, useEffect, useMemo } from 'react';
import Pagination from '../../components/Pagination';
import MailEntry from './MailEntry';
import { sampleMails } from './mail';
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useNavigate } from 'react-router-dom';
import ShowMail from './ShowMail';
import './index.css';
import { viewAll } from '../../utils/mail';

const pageSize = 5;

function WebMail() {
  const [mails, setMails] = useState([]);
  const [currentPage, setCurrentPage] = useState(0);
  useEffect(() => {
    setCurrentPage(1);
  }, []);
  
  const [displayMailIdx, setDisplayMailIdx] = useState(-1);
  const getAllMails = () => {
    viewAll().then(res => res.json()).then(actualData => {
      // actualData.sort((a, b) => b.time - a.time);
      actualData.sort((a, b) => a.mailId - b.mailId);
      setMails(actualData);
    });
  };
  useEffect(() => {
    getAllMails();
  }, []);
  const handleMailClick = (idx, page) => (e) => {
    e.preventDefault();
    setDisplayMailIdx(idx + (page - 1) * pageSize);
  };
  const navigate = useNavigate();
  const handleChangePage = (path) => {
    navigate(`${path}`);
  };
  const currentTableData = useMemo(() => {
    const firstPageIndex = (currentPage - 1) * pageSize;
    const lastPageIndex = firstPageIndex + pageSize;
    return mails.slice(firstPageIndex, lastPageIndex);
  }, [currentPage, mails]);
  const handleRefresh = () => {
    getAllMails();
  };
  return (
    <Container className='mt-5'>
      <Button variant="primary" onClick={handleRefresh}>
            Refresh
      </Button>
      <Row>
        <Col xs={7}>
          <div className='d-flex justify-content-center my-2'>
            <Button variant="primary" size='lg' onClick={(e) => handleChangePage('/mail/compose')}>Compose</Button>
          </div>
          <div className='d-flex justify-content-center my-2'>
            <Pagination
              className="pagination-bar"
              currentPage={currentPage}
              totalCount={mails.length}
              pageSize={pageSize}
              onPageChange={page => setCurrentPage(page)}
            />
          </div>
          <Row className='p-3 heading'>
            <Col xs={3}>
              Sender
            </Col>
            <Col xs={3}>
              Time
            </Col>
            <Col xs={6}>
              Subject
            </Col>
          </Row>
    
          {currentTableData.map((item, index) => {
            return (<MailEntry key={`question-${index + (currentPage - 1) * pageSize}`} mail={item} refresh={getAllMails} onClick={handleMailClick(index , currentPage)} />);
          })}
        </Col>
        <Col xs={5}>
          {displayMailIdx === -1 ? <>Click a message on the left to view details</> :
            <ShowMail mail={mails[displayMailIdx]} />}
        </Col>

      </Row>
      {/*  */}
        
      
    </Container>
  );
}

export default WebMail;